.seat-page {
  background: #e9f0f5;
  min-height: 100vh;
  padding: 1rem 3rem;
  position: relative;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
.movie-info h3 {
  margin: 0;
  font-weight: 600;
}
.movie-info p {
  margin: 0;
  font-size: 0.9rem;
  color: #666;
}
.back-btn {
  margin-right: 1rem;
  border: none;
  background: transparent;
  font-size: 1.5rem;
  cursor: pointer;
}
.ticket-btn {
  border: 1px solid #d33;
  color: #d33;
  background: transparent;
  border-radius: 8px;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

/* matrix */
.matrix-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-top: 1.5rem;
}

.row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 100%;
  max-width: 850px; /* keeps it centered */
  position: relative;
}

.row-label {
  position: absolute;
  left: -30px;
  font-weight: 600;
  color: #333;
  font-size: 0.9rem;
  width: 20px;
  text-align: right;
}

.row-seats {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 8px;
  margin-left: 20px;
}


.seat {
  width: 26px;
  height: 26px;
  border-radius: 4px;
  border: 1.5px solid #ccc;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0,0,0,0.08);
  cursor: pointer;
  transition: transform 0.1s ease, background 0.2s ease;
}

.seat:hover {
  transform: scale(1.1);
}

.seat.booked {
  background: #dcdcdc;
  border-color: #bcbcbc;
  cursor: not-allowed;
}

.seat.selected {
  background: #000;
  border-color: #000;
}


/* footer */
.bottom-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 2rem;
  background: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
.pay-btn {
  background: #000;
  color: #fff;
  border: none;
  padding: 0.7rem 1.8rem;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
}
.pay-btn:disabled {
  background: #aaa;
  cursor: not-allowed;
}

/* dialog */
.overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}
.dialog {
  background: #fff;
  padding: 2rem 3rem;
  border-radius: 10px;
  text-align: center;
}
.dialog h2 { margin-bottom: 1.5rem; }
.count-options {
  display: flex;
  gap: 1rem;
  justify-content: center;
}
.count-options button {
  width: 2.5rem;
  height: 2.5rem;
  border: none;
  border-radius: 50%;
  background: #f3f3f3;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.2s;
}
.count-options button:hover {
  background: #000;
  color: #fff;
}


.screen-indicator {
  margin: 1rem auto;
  width: 60%;
  text-align: center;
  border-top: 3px solid #bbb;
  color: #666;
  font-size: 0.9rem;
  padding-top: 0.4rem;
}


.price-section {
  margin-top: 2.5rem;
  margin-bottom: 0.8rem;
  font-weight: 600;
  font-size: 1.1rem;
  text-align: center;
  color: #333;
}

.row-group {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 100%;
  margin-bottom: 8px;
  position: relative;
}

.row-group .row-label {
  position: absolute;
  left: -30px;
  font-weight: 500;
  color: #333;
}

.price-section:not(:first-child) {
  border-top: 1px dashed #bbb;
  padding-top: 1.5rem;
}
